import "./Search.css"

export const Search = ({ onFilter, filterValue, onAdd }) => {
    return (
        <div className="search__container">
            <img
                alt="plus"
                src="data:image/png;base64,"
                className="modal__add__img"
                onClick={onAdd}
            />
            <label htmlFor="">
                Шукати:
                <input
                    className="input__search"
                    type='search'
                    value={filterValue}
                    onChange={(e) => onFilter(e.target.value)}
                />
            </label>
        </div>
    );
};